<template>
  <div class="navBar">
    <span class="logo"><img src="@/assets/logo.jpeg" alt=""/></span>
    <p class="title">dorryLiu 2020</p>
    <div class="erCode">
      <span class="font" @click="showErcode">点击查看二维码({{ time }}s)</span>
      <p class="code" v-show="ercode">
        <img src="@/assets/ercode.png" alt="" />
      </p>
    </div>
  </div>
</template>
<script>
export default {
  name: "navBar",
  data() {
    return {
      ercode: false,
      time: 5,
      change: false
    };
  },
  methods: {
    throttle(fn, delay) {
      let canRun = true;
      return () => {
        if (!canRun) return;
        canRun = false;
        setTimeout(() => {
          fn.apply(this, arguments);
          canRun = true;
        }, delay);
      };
    },
    showErcode() {
      this.ercode = true;
      let timerr = null;
      clearInterval(timerr);
      if (!this.change) {
        this.change = true;
        timerr = setInterval(() => {
          this.time--;
        }, 1000);
      } else {
        clearInterval(timerr);
        return false;
      }

      this.throttle(() => {
        this.ercode = false;
        this.time = 5;
        this.change = false;
        clearInterval(timerr);
      }, 5000)();
    }
  }
};
</script>
